	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Schabat Schalom","date":"14 Sep 2004","slug":"/2004-09-14-schabat-schalom/","tags":"Y2004"},"html":"<p>NDRInfo, our regional news radio station, runs a Jewish culture program on Fridays called <a href=\"http://www.ndrinfo.de/ndrinfo_pages_std/0,2758,SPM690,00.html\">Schabat Schalom</a>. Last week (or was it the week before) they had a nice talk on bi- and multi-lingual puns. Plays on words always involve some sort of secret knowledge to get the joke, and if you have to explain yourself, the joke is lost. For example, I titled my last post on the Tower of Light tribute in NYC <em>Leuchtturm</em> (\"lighthouse\"), which breaks down into \"light\" and \"tower\". Only a non-native speaker of German would think of calling the tribute a <em>Leuchtturm</em>, and now that I've explained it, the humor is gone...</p>\n<p>Anyway, the rabbi last week explained how \"sabos\" were the course wooden shoes of early industrial workers in France, and when they would protest their poor working conditions by stuffing their shoes into the machines, they were committing \"sabotage\". In the same way, the sabbath forces our hectic way of life to come to a halt for at least one day a week, a form of \"Schabat-tage\". And if you choose to celebrate the sabbath by buying expensive Italian olive-oil bread, you then have \"Ciabat-tage\", which if you pronounce Italian poorly enough sounds nearly the same. </p>\n<p>A French-English-Hebrew-Italian pun, told in German. I was impressed.</p>"},"next":{"fields":{"title":"Exact Change","date":"14 Sep 2004","slug":"/2004-09-14-exact-change/"}},"previous":{"fields":{"title":"Spoon-Feed or Fork-Feed?","date":"14 Sep 2004","slug":"/2004-09-14-spoon-feed-or-fork-feed/"}}},{"node":{"fields":{"title":"Exact Change","date":"14 Sep 2004","slug":"/2004-09-14-exact-change/","tags":"Y2004"},"html":"<p><a title=\"FAZ: Scrap Metal\" href=\"http://www.faz.com/IN/INtemplates/eFAZ/archive.asp?doc={CC739DD9-02EF-4141-B80D-982B122772A1}\">Exact change, a German obsession</a> <a href=\"http://blogeline.blogspot.com/2004/09/german-small-change.html\">(via Blogeline)</a></p>"},"next":{"fields":{"title":"Leuchtturm","date":"13 Sep 2004","slug":"/2004-09-13-leuchtturm/"}},"previous":{"fields":{"title":"Schabat Schalom","date":"14 Sep 2004","slug":"/2004-09-14-schabat-schalom/"}}},{"node":{"fields":{"title":"Leuchtturm","date":"13 Sep 2004","slug":"/2004-09-13-leuchtturm/","tags":"Y2004"},"html":"<p><a href=\"http://www.overshadowed.com/mt/archives/000250.html\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/09/leuchtturm.jpg\" border=\"0\" title=\"Link to full image\" alt=\"\" /> </a></p>\n<p><a href=\"http://www.overshadowed.com/mt/archives/000250.html\">NYC Tribute in Light by Keith Kin Yan</a> <a href=\"http://www.kottke.org/remainder/04/09/6477.html\">(via kottke)</a></p>\n<p>Ich geh mit dir wohin du willst<br />\nAuch bis ans Ende dieser Welt<br />\nAm Meer, am Strand, wo Sonne scheint<br />\nWill ich mit dir alleine sein<br />\n<a href=\"http://phobos.apple.com/WebObjects/MZStore.woa/wa/viewAlbum?playlistId=16259783&selectedItemId=16259810\">Nena, Leuchtturm (1983)</a></p>\n<p><em>I'll go with you wherever you want<br />\nEven to the end of the world<br />\nOn the sea, on the beach, where the sun shines<br />\nI want to be alone with you</em></p>"},"next":{"fields":{"title":"Mama on the Beach","date":"12 Sep 2004","slug":"/2004-09-12-mama-on-the-beach/"}},"previous":{"fields":{"title":"Exact Change","date":"14 Sep 2004","slug":"/2004-09-14-exact-change/"}}}],"pathPrefix":"","first":false,"last":false,"index":422,"pageCount":1101}}

